import * as React from 'react'
import * as ReactDOM from 'react-dom'
import * as PropTypes from 'prop-types'
import Table from 'src/packages/table/Table'

const componentName = 'TableExample'

class TableExample extends React.Component<{}, any> {
  public static displayName = componentName

  public static propTypes = {}

  public static defaultProps = {}

  constructor(props: {}) {
    super(props)
    const array = []
    for (let i = 0; i < 20; i++) {
      array.push({
        key: `name${i + 1}`,
        name: `name${i + 1}`,
        age: i + 1,
        address: `西湖区湖底公园${i + 1}号`
      })
    }
    this.state = {
      columns: [
        {
          title: '姓名',
          key: 'name'
        },
        {
          title: '年龄',
          key: 'age'
        },
        {
          title: '住址',
          key: 'address'
        },
        {
          title: 'Action',
          key: 'action',
          extra: <div style={{ cursor: 'pointer' }}>删除</div>,
          onClick: (key: string) => this.handleClickAction(key)
        }
      ],
      dataSource: array
    }
  }

  public handleClickAction = (key: string) => {
    this.setState({
      dataSource: this.state.dataSource.filter((data: any) => data.key !== key)
    })
  }

  public render() {
    const cn = componentName

    return (
      <div className="table-example">
        <div className="example">
          <div className="container" style={{ display: 'block' }}>
            <Table
              columns={this.state.columns}
              dataSource={this.state.dataSource}
            />
          </div>
        </div>
      </div>
    )
  }
}

export default TableExample



